<template>
	<view>
		<view class="status-bar" :style="{
			height: statusBarHeight + 'px'
		}"></view>

		<view class="nav-bar" :style="{
			height: navHeight + 'px'
		}">

			<!-- #ifdef MP-WEIXIN -->
			<image :src="$kt.file.byPath('image/icon/search.svg')" :style="{
				right: (menuButton.width - (-15)) + 'px'
			}" mode="heightFix" class="nav-bar-icon">
			</image>
			<!-- #endif -->
			<!-- #ifdef APP-PLUS -->
			<image :src="$kt.file.byPath('image/icon/search.svg')" :style="{
				'left': '670rpx'
			}" mode="heightFix" class="nav-bar-icon">
			</image>
			<!-- #endif -->

			<text class="nav-bar-title">
				{{ "ᑋᵉᑊᑊᵒ ᵕ̈ ᑋᵉᑊᑊᵒ" }}
			</text>



		</view>

		<view id="tView" v-if="screenTop >= 98" style="
																	padding: 30rpx 30rpx 0 30rpx;
																	box-sizing: border-box;
																	position: fixed;
																	top: 0;
																	z-index: 999;
																	width: 100%;
																	background-color: #fff;
																	left: 0;
																" :style="{
																	'top': (statusBarHeight - (-navHeight) - 1) + 'px'
																}">

			<view class="t">
				<text @click="toCheckSection('userListOfRecommend')"
						:class="'tt ' + (checkSection == 'userListOfRecommend' ? 'tt-active' : 'tt-no-active')">推荐</text>
					<text @click="toCheckSection('userListOfNear')"
						:class="'tt ' + (checkSection == 'userListOfNear' ? 'tt-active' : 'tt-no-active')">附近</text>
				<image :src="$kt.file.byPath('image/screen.svg')" mode="widthFix" style="
																									width: 40rpx;
																									height: 40rpx;
																									float: right;
																									"></image>

			</view>
		</view>

		<scroll-view @scrolltolower="onScrollToLower" :scroll-y="true" @scroll="onScroll" :style="{
			'height': 'calc(100vh - ' + (statusBarHeight - (-navHeight)) + 'px - 112rpx)'
		}">
			<grid></grid>


			<view id="tView" style="
																				padding: 30rpx 30rpx 0 30rpx;
																				box-sizing: border-box;
																			">

				<view class="t">
					<text @click="toCheckSection('userListOfRecommend')"
						:class="'tt ' + (checkSection == 'userListOfRecommend' ? 'tt-active' : 'tt-no-active')">推荐</text>
					<text @click="toCheckSection('userListOfNear')"
						:class="'tt ' + (checkSection == 'userListOfNear' ? 'tt-active' : 'tt-no-active')">附近</text>

					<image :src="$kt.file.byPath('image/screen.svg')" mode="widthFix" style="
																									width: 40rpx;
																									height: 40rpx;
																									float: right;
																									"></image>

				</view>
			</view>


			<view id="tView" style="padding: 0rpx 30rpx 0 30rpx;box-sizing: border-box;">
				<userListOfRecommend v-show="checkSection == 'userListOfRecommend'"></userListOfRecommend>
				<userListOfNear v-if="userListOfNearIf" v-show="checkSection == 'userListOfNear'"></userListOfNear>

			</view>
		</scroll-view>


		<u-toast ref="uToast" />


	</view>
</template>

<script>
import grid from './inIndex/grid.vue';
import userListOfRecommend from './inIndex/userListOfRecommend.vue';
import userListOfNear from './inIndex/userListOfNear.vue';

export default {
	components: {
		grid,
		userListOfRecommend,
		userListOfNear
	},
	data() {
		let appFront = this.$kt.i18n.getAppFront();
		return {
			appFront,
			statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
			navHeight: 48,
			// #ifdef MP-WEIXIN
			menuButton: wx.getMenuButtonBoundingClientRect(),
			// #endif
			screenTop: 0,
			checkSection: "",
			userListOfNearIf: false,
		};
	},
	mounted() {
		this.checkSection = "userListOfRecommend";

		this.navHeight = 44;
		// #ifdef MP-WEIXIN
		this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
		// #endif

	},
	methods: {
		onScroll(res) {
			this.screenTop = res.target.scrollTop;
		},
		onScrollToLower() {
			// console.log('onScrollToLower');
			uni.$emit('pageIndexOnScrollToLower');
		},
		openSetting() {
			uni.openSetting({
				success: (res) => {
					if (res.authSetting["scope.userLocation"]) {
						this.getLocation();
					}
				}
			});
		},
		getLocation() {

			var section = "userListOfNear";
			if (this.userListOfNearIf) {
				this.checkSection = section;
				this.$forceUpdate();
				return false;
			}
			uni.getLocation({
				type: 'gcj02',
				success: (res) => {
					uni.showLoading({
						title: '加载中'
					});
					var latitude = res.latitude.toFixed(5);
					var longitude = res.longitude.toFixed(5);

					this.$kt.request.request({
						api: this.$api.business.ovo.userBind.updateLocation,
						data: {
							latitude,
							longitude
						},
						stateSuccess: (res1) => {
							this.$ovo.setSelf(res1.data);
							this.userListOfNearIf = true;
							this.checkSection = section;
							uni.hideLoading();
							this.$forceUpdate();

						},
						stateFail: (res1) => {
							this.$refs.uToast.show({
								title: '获取位置失败',
								type: 'error'
							});

							uni.hideLoading();
						},
						fail: (err) => {
							this.$refs.uToast.show({
								title: '未知错误',
								type: 'error'
							});
							uni.hideLoading();
						}
					})
				},
				fail: (err) => {
					console.log(err);
					if (err.errMsg == "getLocation:fail auth deny") {
						uni.hideLoading();
						this.userListOfNearIf = false;
					}
				}
			});
		},
		toCheckSection(section) {
			if (section == "userListOfNear") {
				uni.getSetting({
					success: (res) => {
						console.log(res);
						if (res.authSetting["scope.userLocation"] == null) {
							this.getLocation();
						}
						else if (!res.authSetting["scope.userLocation"]) {
							this.openSetting();
						} else {
							this.getLocation();
						}
					}
				});
			} else {
				this.checkSection = section;
			}
		}
	},
}
</script>

<style lang="scss" scoped>
.status-bar {
	background-color: #fff;
}

.nav-bar {
	position: relative;
	background-color: #fff;

	.nav-bar-icon {
		position: absolute;
		width: 40rpx;
		height: 40rpx;
		top: 50%;
		transform: translateY(-50%);
	}

	.nav-bar-icon:active {
		transform: scale(0.9) translateY(-50%);
	}

	.nav-bar-title {
		position: absolute;
		left: 20rpx;
		font-size: 42rpx;
		top: 50%;
		transform: translateY(-50%);
		// 文字间距
		// letter-spacing: 5rpx;
	}
}

.t {

	padding-bottom: 30rpx;

	.tt {
		display: inline-block;
		font-size: 32rpx;
		margin-right: 30rpx;
		vertical-align: bottom;
	}

	.tt-no-active:active {
		opacity: 0.5;
	}

	.tt-active {
		font-weight: bold;
	}


}
</style>




















